.profile{
  letter-spacing: 2.7px;
  font-size: 17px;
  line-height: 1.5;
}

.personal{
  float: left;
  width: 37%;
  min-height: 100vh;
  text-align: center;
  background: #333;
  color: #FFF;
}

.experience{
  float: left;
  width: 63%;
  height: 100%;
}

.personal_container{
  padding: 45px 5%;
}

.photo{
  max-width: 100%;
  max-height: 200px;
}

.fullname{
  margin-top: 30px;
  font-size: 4.3vw;
  font-weight: 900;
  word-spacing: 100vw;
  letter-spacing: 3.3px;
  line-height: 1.2;
  text-transform: uppercase;
}

.profession{
  margin-top: 10px;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
}

.separator{
  height: 3px;
  width: 92%;
  margin: 0 auto;
  border: none;
  background: #9E9E9E;
}

.user_description{
  max-width: 285px;
  margin: 0 auto;
}

@media (max-width: 768px){
  .personal{
    float: none;
    width: 100%;
  }
  
  .experience{
    float: none;
    width: 100%;
  }

  .fullname{
    font-size: 40px;
  }
}
